<template>
  <div id="sub_wrap">
    <h1>방문차량 할인</h1>
    <div class="content_wrap">
      <div class="menu_title">
        <form>
          <span class="small_title">날짜</span>
          <input
            type="datetime-local"
            id="start_time"
            v-model="start_time"
            placeholder="2020.03.20 오전 12:00"
          />
          <span>~</span>
          <input
            type="datetime-local"
            id="end_time"
            v-model="end_time"
            placeholder="2020.03.20 오후 11:59"
          />
          <span class="search_icon small_title">차량번호</span>
          <input
            class="search_icon"
            maxlength="9"
            type="text"
            placeholder="차량 번호를 입력해 주세요."
            v-model="searchNumber"
          />
          <button class="seach_confirm" @click.prevent="search">검색</button>
          <button @click.prevent="registNew" class="savebtn">저장</button>
        </form>
      </div>
      <div class="list-items">
        <ul class="list-title">
          <li class="list-number">
            <span>번호</span>
          </li>
          <li class="list-vehicle-number">
            <span
              >차량번호<button @click.prevent="item_lp_click">
                <font-awesome-icon icon="caret-down" v-if="sort_item.lp" />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-in-time">
            <span
              >입차시각<button @click.prevent="in_time_click">
                <font-awesome-icon icon="caret-down" v-if="sort_item.in_time" />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-out-time">
            <span
              >출차시각<button @click.prevent="out_time_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.out_time"
                />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-confirm-time">
            <span
              >방문자 확인시각<button @click.prevent="confirmTime_click">
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.confirmTime"
                />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-sale-time">
            <span
              >방문자 확인시각<button
                @click.prevent="discounted_duration_click"
              >
                <font-awesome-icon
                  icon="caret-down"
                  v-if="sort_item.discounted_duration"
                />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-resident-id">
            <span
              >입주사 ID<button @click.prevent="id_click">
                <font-awesome-icon icon="caret-down" v-if="sort_item.id" />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
          <li class="list-resident-fee">
            <span
              >입주사 부담금액<button @click.prevent="charge_click">
                <font-awesome-icon icon="caret-down" v-if="sort_item.charge" />
                <font-awesome-icon icon="caret-up" v-else /></button
            ></span>
          </li>
        </ul>
        <ul class="list-wrap">
          <li v-for="(c, index) in residents" :key="index">
            <span class="list-number"> {{ index + 1 }}</span>
            <input
              class="list-vehicle-number"
              type="text"
              v-model="c.lp"
              readonly
            />
            <span class="list-in-time">{{ c.in_time }}</span>
            <span class="list-out-time">{{ c.out_time }}</span>
            <span class="list-confirm-time">{{ c.confirmTime }}</span>
            <input
              class="list-sale-time"
              type="text"
              v-model="c.discounted_duration"
            />
            <input
              class="list-resident-id"
              type="text"
              v-model="c.id"
              readonly
            />
            <input
              class="list-resident-pay"
              type="text"
              v-model="c.charge"
              readonly
            />

            <!--	<button v-if="d.get_image_display" @click.prevent="open_picture(d)">보기<font-awesome-icon icon="angle-down" /></button>
						<button v-else @click="close_picture(d)">닫기<font-awesome-icon icon="angle-up" /></button>	-->
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import _ from "underscore"
export default {
  data() {
    return {
      start_time: new Date(),
      end_time: new Date(),
      searchNumber: "",
      btn: "",
      residents: [
        /*{
				order: "0",
				lp: "29가1234",
				time:  format_time1(new Date(1586297735243)),
				confirmTime: format_time1(new Date(1586297735243)),
				discounted_duration : "60",
				charge: "1000",
				imageName: require("../assets/car_test1.jpg"),
				imageShow: false
				}*/
      ],
      sort_item: {
        lp: false,
        in_time: false,
        out_time: false,
        confirmTime: false,
        discounted_duration: false,
        id: false,
        charge: false
      }
    }
  },
  methods: {
    item_lp_click: function() {
      let temp10 = []
      if (this.sort_item.lp === false) {
        this.sort_item.lp = true
        temp10 = _.sortBy(this.residents, "lp")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.lp = false
        temp10 = _.sortBy(this.residents, "lp")
        return (this.residents = temp10)
      }
    },
    in_time_click: function() {
      let temp10 = []
      if (this.sort_item.in_time === false) {
        this.sort_item.in_time = true
        temp10 = _.sortBy(this.residents, "in_time")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.in_time = false
        temp10 = _.sortBy(this.residents, "in_time")
        return (this.residents = temp10)
      }
    },
    out_time_click: function() {
      let temp10 = []
      if (this.sort_item.out_time === false) {
        this.sort_item.out_time = true
        temp10 = _.sortBy(this.residents, "out_time")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.out_time = false
        temp10 = _.sortBy(this.residents, "out_time")
        return (this.residents = temp10)
      }
    },
    confirmTime_click: function() {
      let temp10 = []
      if (this.sort_item.confirmTime === false) {
        this.sort_item.confirmTime = true
        temp10 = _.sortBy(this.residents, "confirmTime")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.confirmTime = false
        temp10 = _.sortBy(this.residents, "confirmTime")
        return (this.residents = temp10)
      }
    },
    discounted_duration_click: function() {
      let temp10 = []
      if (this.sort_item.discounted_duration === false) {
        this.sort_item.discounted_duration = true
        temp10 = _.sortBy(this.residents, "discounted_duration")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.discounted_duration = false
        temp10 = _.sortBy(this.residents, "discounted_duration")
        return (this.residents = temp10)
      }
    },
    id_click: function() {
      let temp10 = []
      if (this.sort_item.id === false) {
        this.sort_item.id = true
        temp10 = _.sortBy(this.residents, "id")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.id = false
        temp10 = _.sortBy(this.residents, "id")
        return (this.residents = temp10)
      }
    },
    charge_click: function() {
      let temp10 = []
      if (this.sort_item.charge === false) {
        this.sort_item.charge = true
        temp10 = _.sortBy(this.residents, "charge")
        temp10.reverse()
        return (this.residents = temp10)
      } else {
        this.sort_item.charge = false
        temp10 = _.sortBy(this.residents, "charge")
        return (this.residents = temp10)
      }
    },
    socket_process(data) {
      console.log("socket_process===>", data)
      if (
        data.status === "ok" &&
        data.kind === "get_resident_discounted_list_admin_out"
      ) {
        console.log("socket_process for ===> : ", data)
        for (var i = 0; i < data.docs.length; i++) {
          var obj1 = data.docs[i]
          if (obj1.obj_in) {
            var obj3 = obj1.obj_in
            for (var j = 0; j < obj3.discounted_list.length; j++) {
              for (var k = j + 1; k < obj3.discounted_list.length; k++) {
                if (obj3.discounted_list[j].id === obj3.discounted_list[k].id) {
                  obj3.discounted_list[k].splice(k, 1)
                }
              }
              if (this.searchNumber === "") {
                let obj2 = {}
                obj2.image_name = //       "http://test.sunil-ezp.com/images/" + obj1.folder_name + "/" + obj1.fname
                  "http://localhost:3002/images/" +
                  obj1.folder_name +
                  "/" +
                  obj1.fname
                console.log(" obj2.image_name : ", obj2.image_name)
                obj2.lp = obj1.lp
                obj2.time = format_time1(new Date(obj3.loop_event_time))
                obj2.out_time = format_time1(new Date(obj1.loop_event_time))
                obj2.confirmTime = format_time1(
                  new Date(obj3.discounted_list[j].discounted_time)
                )
                obj2.discounted_duration =
                  Math.ceil(
                    parseInt(obj3.discounted_list[j].discounted_duration),
                    10
                  ) /
                  (60 * 1000)
                obj2.id = obj3.discounted_list[j].id
                obj2.charge = obj3.discounted_list[j].fee
                obj2.imageShow = false
                this.residents.push(obj2)
                break
              } else {
                if (obj1.lp.search(this.searchNumber) >= 0) {
                  let obj2 = {}
                  obj2.image_name =
                    "http://localhost:3002/images/" +
                    obj1.folder_name +
                    "/" +
                    obj1.fname
                  obj2.lp = obj1.lp
                  obj2.time = format_time1(new Date(obj3.loop_event_time))
                  obj2.out_time = format_time1(new Date(obj1.loop_event_time))
                  obj2.confirmTime = format_time1(
                    new Date(obj3.discounted_list[j].discounted_time)
                  )
                  obj2.discounted_duration =
                    Math.ceil(
                      parseInt(obj3.discounted_list[j].discounted_duration),
                      10
                    ) /
                    (60 * 1000)
                  obj2.id = obj3.discounted_list[j].id
                  obj2.charge = obj3.discounted_list[j].fee
                  obj2.imageShow = false
                  this.residents.push(obj2)
                  break
                }
              }
            }
          } else {
            console.log("obj_in이 없네요!!!!")
          }
        }
        //console.log(this.vehicleInformation.length);
        console.log(this.residents.length)
        console.log(this.residents)
      }
    },
    listAdd() {
      // this.dataList = this.inputList
      this.dataList.unshift({
        getTimer: this.listTimer,
        getPc: this.listPc,
        getPlace: this.listPlace,
        getWay: this.listWay,
        getCarNumer: this.listCarNumer,
        getType: this.listType,
        getLocation: this.listLocation,
        getMemo: this.listMemo
      })
      this.listTimer = ""
      this.listPc = ""
      this.listPlace = ""
      this.listWay = ""
      this.listCarNumer = ""
      this.listType = ""
      this.listLocation = ""
      this.listMemo = ""
    },
    date_set() {
      let d = new Date()
      //let d1 = d - (24*60*60*1000);
      //console.log("d : ", d, " d1 : ", d1);
      var str_start =
        d.getFullYear() +
        "-" +
        itoStr(d.getMonth() + 1) +
        "-" +
        itoStr(d.getDate()) +
        "T00:00"
      var str_end =
        d.getFullYear() +
        "-" +
        itoStr(d.getMonth() + 1) +
        "-" +
        itoStr(d.getDate()) +
        "T23:59"
      this.start_time = str_start
      this.end_time = str_end
    },
    sendMessage(c) {
      this.$socket.emit("message_from_web_client", c)
    },
    search() {
      this.residents = []
      var obj = {}
      obj.kind = "get_resident_discounted_list_admin_out"
      obj.lp = this.searchNumber
      obj.start_time = get_start_time(this.start_time)
      obj.end_time = get_end_time(this.end_time)
      obj.id = window.localStorage.getItem("id")
      obj.pw = window.localStorage.getItem("pw")
      console.log("lp_search() : ", obj)
      this.sendMessage(obj)
    },
    openImg(c) {
      return (c.imageShow = true)
      //console.log(" openImg c : ", c);
    },
    closeImg(c) {
      return (c.imageShow = false)
      // console.log(" closeImg c : ", c);
    }
  },
  created() {
    this.$socket.on("message_from_server", data => {
      console.log("resident_discount_data.kind : ", data)
      this.socket_process(data)
    })
    this.date_set()
    window.addEventListener("scroll", this.handleScroll)
  }
}

function format_time1(dat) {
  var str = ""
  str += dat.getFullYear()
  str += "." + num_to_str(dat.getMonth() + 1)
  str += "." + num_to_str(dat.getDate())
  str += ". " + num_to_str(dat.getHours())
  str += ":" + num_to_str(dat.getMinutes())
  str += ":" + num_to_str(dat.getSeconds())
  return str
}

function num_to_str($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}
/*
function get_datetime(str) {
	var res = str.split(/[-T.:\s]+/);
	var d = new Date(parseInt(res[0]), parseInt(res[1])-1, parseInt(res[2]), parseInt(res[3]), parseInt(res[4]), 0, 0);
	return d.getTime();
} */

function get_start_time(str) {
  var res = str.split(/[-T.:\s]+/)
  var d = new Date(
    parseInt(res[0]),
    parseInt(res[1]) - 1,
    parseInt(res[2]),
    parseInt(res[3]),
    parseInt(res[4]),
    0,
    0
  )
  return d.getTime()
}

function get_end_time(str) {
  var res = str.split(/[-T.:\s]+/)
  var d = new Date(
    parseInt(res[0]),
    parseInt(res[1]) - 1,
    parseInt(res[2]),
    parseInt(res[3]),
    parseInt(res[4]),
    59,
    999
  )
  return d.getTime()
}

function itoStr($num) {
  $num < 10 ? ($num = "0" + $num) : $num
  return $num.toString()
}
</script>
